<template>
    <div class="msiteWrap">
        <header>
            <router-link to="">
                <i class="el-icon-search"></i>
            </router-link>
            <router-link to="/home">
                <span class="ellipsis">{{headTitle ? headTitle.name : ""}}</span>
            </router-link>
            <router-link to="/login">
                <span>登录|注册</span>
            </router-link>
        </header>
        <nav>
            <div>
                <div class="swiper-container">
                    <ul>
                        <li v-for="(item,index) in navDetails" :key="item.id" @click="foodClass(item)">
                            <img :src="'https://fuss10.elemecdn.com'+item.image_url" alt="" :title="item.description">
                            <p>{{item.title}}</p>
                        </li>
                    </ul>
                </div>
                <div class="pagination">
                    <span @click="changeBgc((addBgc ? pagchoose : pagno).background, 'btn1')" :style="addBgc ? pagchoose : pagno">
                    </span>
                    <span @click="changeBgc((addBgc ? pagno : pagchoose).background, 'btn2')" :style="addBgc ? pagno : pagchoose">
                    </span>
                </div>
            </div>
        </nav>
        <div class="shopList">
            <p>
                <i class="el-icon-s-shop"></i>
                <span>附近商家</span>
            </p>
            <div>
                <ul>
                    <li v-for="(item, index) in shop" :key="item.id" class="shop_li" @click="toShop(item.id)">
                        <section><img :src="'https://elm.cangdu.org/img/'+item.image_path" alt=""></section>
                        <div>
                            <div class="shop_title">
                                <h4><span>品牌</span>{{item.name}}</h4>
                                <ul class="supports">
                                    <li>保</li>
                                    <li>准</li>
                                    <li>票</li>
                                </ul>
                            </div>
                            <h5 class="rating_num">
                                <div class="rating_left">
                                    <el-rate :value="item.rating" disabled show-score  text-color="#ff6000" disabled-void-color="#d1d1d1">
                                    </el-rate>
                                    <span>月售{{item.recent_order_num}}单</span>
                                </div>
                                <div class="rating_right">
                                    <span>蜂鸟专送</span>
                                    <span>准时达</span>
                                </div>
                            </h5>
                            <h5 class="fee_distance">
                                <p>¥{{item.float_minimum_order_amount}}起送/配送费约¥{{item.float_delivery_fee}}</p>
                                <p><span class="distance">{{item.distance}}/</span><span class="hour">{{item.order_lead_time}}</span></p>
                            </h5>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Msite",
        data(){
            return {
                headTitle:null,
                navDetails:[],
                addBgc:true,
                pagchoose:{opacity: 1, background: "#007aff"},
                pagno:{opacity: .2, background: "#000"},
                shop:[],
                geohash:""
            }
        },
        created(){
            this.$parent.isChoose.id = 1;
            this.geohash = localStorage.getItem("geohash");
            let latitude = this.geohash.split(",")[0];
            let longitude = this.geohash.split(",")[1];
            this.axios.get("/v2/pois/"+this.geohash).then(res=>{
                this.headTitle = res;
            }).catch(err=>{});
            this.axios.get("/v2/index_entry").then(res=>{
                this.navDetails = res;
            }).catch(err=>{});
            this.axios.get("/shopping/restaurants",{
                params:{
                    latitude:latitude,
                    longitude:longitude
                }
            }).then(res=>{
                this.shop = res;
            }).catch(err=>{});
        },
        methods:{
            changeBgc(bgc, btn){
                let ul = document.querySelector(".swiper-container>ul")
                if(bgc != "#007aff"){
                    this.addBgc = !this.addBgc;
                    if(btn == "btn1"){
                        ul.style.left = 0;
                    }else {
                        ul.style.left = -(ul.offsetWidth/2) + "px";
                    }
                }
            },
            foodClass(food){
                console.log(localStorage.getItem("geohash"));
                this.$router.push({path:"/food",query:{geohash:localStorage.getItem("geohash"),title:food.title,restaurant_category_id:food.id}});
                console.log(food);
            },
            toShop(i){
                this.axios.get("/shopping/restaurant/"+i).then(res=>{
                    localStorage.setItem("current_shop",JSON.stringify(res));
                    this.$router.push({path:"/shop"});
                }).then(err=>{});
            }
        }
    }
</script>

<style>
    .el-rate {
        width: 2.98rem;
        height: .6rem;
        display: flex;
        align-items: flex-end;
    }
    .el-rate__icon {
        margin: 0 -.08rem 0 0;
        font-size: .45rem;
    }
    .el-rate__text {
        font-size: .45rem;
        color: #ff6000;
        margin: 0 .2rem .1rem .3rem;
    }
</style>
<style scoped>
    header>a {
        color: #fff;
    }
    header>a:first-child {
        left: .8rem;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        font-size: .65rem;
    }
    header>a:nth-child(2) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        text-align: center;
        margin-left: -.5rem;
        font-size: .8rem;
        display: block;
    }
    header>a:nth-child(2)>span {
        width: 100%;
        display: block;
    }
    header>a:last-child {
        right: .55rem;
        font-size: .65rem;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    nav {
        width: 100%;
        padding-top: 2.1rem;
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        height: 8rem;
    }
    .swiper-container {
        width: 100%;
        overflow: hidden;
    }
    .swiper-container ul {
        width: 200%;
        overflow: hidden;
        transition: all .5s;
        position: relative;
        top: 0;
        left: 0;
    }
    .swiper-container li {
        width: 12.5%;
        padding: .3rem 0;
        float: left;
        text-align: center;
    }
    .swiper-container img {
        margin-bottom: .3rem;
        width: 1.8rem;
        height: 1.8rem;
    }
    .swiper-container p {
        text-align: center;
        font-size: .55rem;
        color: #666;
    }
    .pagination {
        position: absolute;
        z-index: 10;
        left: 50%;
        transform: translateX(-50%);
    }
    .pagination>span {
        display: inline-block;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        margin: 0 5px;
    }
    .shopList {
        margin-top: .4rem;
        border-top: .025rem solid #e4e4e4;
        background-color: #fff;
    }
    .shopList>p {
        width: 100%;
        height: 1.64rem;
        color: #999;
        line-height: 1.3rem;
    }
    .shopList>p>i {
        font-size: .7rem;
        margin-right: .3rem;
        margin-left: .6rem;
    }
    .shopList>p>span {
        font-size: .55rem;
    }
    .shopList>div {
        background-color: #fff;
        margin-bottom: 2rem;
    }
    .shop_li {
        display: flex;
        border-bottom: .025rem solid #f1f1f1;
        padding: .7rem .4rem;
    }
    .shop_li>section>img {
        width: 2.7rem;
        height: 2.7rem;
        display: block;
        margin-right: .4rem;
    }
    .shop_li>div {
        flex: auto;
    }
    .shop_title {
        display: flex;
        justify-content: space-between;
    }
    .shop_title>h4 {
        width: 8.5rem;
        color: #333;
        padding-top: .01rem;
        font: .65rem/.65rem PingFangSC-Regular;
        font-weight: 700;
    }
    .shop_title>h4>span {
        font-size: .5rem;
        line-height: .6rem;
        color: #333;
        background-color: #ffd930;
        padding: 0 .1rem;
        border-radius: .1rem;
        margin-right: .2rem;
    }
    .supports {
        display: flex;
        transform: scale(.8);
        margin-right: -.3rem;
    }
    .supports>li {
        font-size: .5rem;
        color: #999;
        border: .025rem solid #f1f1f1;
        padding: 0 .04rem;
        border-radius: .08rem;
        margin-left: .05rem;
    }
    .rating_num {
        display: flex;
        justify-content: space-between;
        height: .6rem;
        margin-top: .52rem;
    }
    .rating_left {
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    .rating_left>span {
        transform: scale(.8);
        font-size: .4rem;
        color: #666;
    }
    .rating_right {
        display: flex;
        align-items: center;
        transform: scale(.7);
        min-width: 5rem;
        justify-content: flex-end;
        margin-right: -.8rem;
    }
    .rating_right>span {
        border: .025rem solid #3190e8;
        font-size: .4rem;
        padding: .04rem .08rem 0;
        border-radius: .08rem;
        margin-left: .08rem;
    }
    .rating_right>span:nth-child(1) {
        color: #fff;
        background-color: #3190e8;
    }
    .rating_right>span:nth-child(2) {
        color: #3190e8;
    }
    .fee_distance {
        margin-top: .52rem;
        display: flex;
        justify-content: space-between;
        font-size: .5rem;
        color: #333;
    }
    .fee_distance>p:first-child {
        transform: scale(.9);
        font-size: .5rem;
        color: #666;
    }
    .fee_distance>p:last-child {
        transform: scale(.9);
    }
    .distance {
        color: #999;
    }
    .hour {
        color: #3190e8;
    }
</style>